import React from 'react'
import Question from './Question'
import { addAnswer, removeAnswer, resetAnswers } from '../reducers/answerReducer'
import { initQuestions } from '../reducers/questionReducer'
import { resetResults} from '../reducers/resultReducer'
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import {connect} from 'react-redux'
import {Table} from 'react-bootstrap'
import '../App.css'

class QuestionList extends React.Component {

  componentWillMount = async() => {
    await this.props.resetResults()
    await this.props.resetAnswers()
    await this.props.initQuestions(this.props.language)
  }

  render() {
    const question = this.props.visibleQuestion

    if(question === undefined) {
      return <div></div>
    }

    const backButton = {display: this.props.answers.length >0 ? '' : 'none'}
    const resultButton = {display: this.props.answers.length >0 ? '' : 'none'}

    if(question === null) {
      return (
        <div>
          <div className="homestyle">
            <p className="resultbox">Almost done. Click results below to see which models match your preferations the most.</p>
          </div>
          <Table>
            <tbody>
              <tr>
                <td><h5 className="blackButton" style={backButton} onClick={this.previousQuest}><a>back</a></h5></td>
                <td><Link to="/quiz/results"><h5 className="blackButton" style={resultButton}>results</h5></Link></td>
              </tr>
            </tbody>
          </Table>
        </div>
      )
    }


    return(
      <div>
        <Question key={question.attribute} question= {question} answerHandler={this.handleAnswer}/>
        <Table>
          <tbody>
            <tr>
              <td><h5 className="blackButton" style={backButton} onClick={this.previousQuest}><a>back</a></h5></td>
              <td><h5 className="blackButton" onClick={this.skipQuestion}><a>skip</a></h5></td>
            </tr>
          </tbody>
        </Table>
        <br/>
        <br/>
      </div>
    )
  }

  skipQuestion = () => {
    this.props.addAnswer(null)
  }

  previousQuest = () => {
    this.props.removeAnswer()
  }

  handleAnswer = (attr, answer) => {
    let newAns = {
      "attribute" : attr,
      "answer" : answer
    }
    this.props.addAnswer(newAns)
  }

}

const questionToShow = (questions, n, attr) => {
  if(questions.length === n){
    return null
  } else {
    const questionToShow = questions.filter(q => q.attribute === questions[n].attribute)
    return questionToShow[0]
  }
}

const mapStateToProps = (state) => {
    return {
      visibleQuestion: questionToShow(state.questions, state.answers.length, state.questions[state.answers.length]),
      answers: state.answers,
      language: state.language
    }
}

export default connect(
  mapStateToProps,
  {addAnswer, removeAnswer,
    resetAnswers,resetResults,
    initQuestions
  }
)(QuestionList)



