import React from 'react'
import '../App.css';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'

const Home = (props) => {

  const title = props.language === 'EN' ? <EnglishTitle/> : <FinnishTitle/>
  const infoBox = props.language === 'EN' ? <EnglishInfo/> : <FinnishInfo/>
    return(
      <div className="homestyle">
        {title}
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Pelago-Brooklyn.jpg" alt="Brooklyn" />
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Hanko-Street.jpg" alt="Hanko Street" />
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Stavanger_Commuter_1.jpg" alt="Stavanger Commuter" />
        <Start/>
        {infoBox}
      </div>
    )
}

const Start = () => {
  return(
    <Link style={{color:"black"}} to="/quiz/start">
      <div className="startBlock">
        <p>start</p>
      </div>
    </Link>
  )
}

const EnglishTitle = () => {
  return(
      <h2>Welcome to Bike Assistant</h2>
  )
}

const FinnishTitle = () => {
  return(
      <h2>Tervetuloa Pyöräsuosittelijaan</h2>
  )
}

const FinnishInfo = () => {
  return(
    <div>
    <p className="home-upperBlock">Vastaa kymmeneen kysymykseen ja katso mikä malli vastaa parhaiten mieltymyksiäsi.</p>
    <p className="home-lowerBlock">[ Voit hypätä kysymyksen yli, jos siihen tuntuu vaikealta vastata. ]</p>
    </div>
  )
}

const EnglishInfo = () => {
  return(
    <div>
      <p className="home-upperBlock">The idea is to give you a recommendation so that you have more than a hunch of what you are after.</p>
      <p className="home-lowerBlock">[ If you find it challenging answering to a question, feel free to skip it. ]</p>
    </div>
  )
}
/*
<Link to="/start"><h5>start</h5></Link>

<div className='languageSelection'>
  <p>
    <a onClick={this.languageChange}>EN</a>
    <a onClick={this.languageChange}>FI</a>
  </p>
</div>
*/
export default Home



